<template>
  <div class="container">
    <el-row type="flex" justify="center">
      <span class="txt_title">请确认您的续住信息</span>
    </el-row>
    <el-row type="flex" justify="center">
      <el-form ref="form" :model="form" label-width="120px">
        <el-row type="flex" class="row-bg" justify="center">
          <el-col :span="8">
            <el-form-item label-width="168px" :disabled="true" label="姓名">
              <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label-width="168px" :disabled="true" label="房型">
              <el-input v-model="form.room_type"> </el-input>
            </el-form-item>
            <el-form-item label-width="168px" :disabled="true" label="续住天数">
              <el-input v-model="form.days"> </el-input>
            </el-form-item>
            <el-form-item label-width="168px" :disabled="true" label="入住日期">
              <el-input v-model="form.date1"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label-width="168px" :disabled="true" label="房号">
              <el-input v-model="form.room_no"> </el-input>
            </el-form-item>
            <el-form-item label-width="168px" :disabled="true" label="房费">
              <el-input v-model="form.rates"> </el-input>
            </el-form-item>
            <el-form-item label-width="168px" :disabled="true" label="收费方式">
              <el-input v-model="form.x"> </el-input>
            </el-form-item>
            <el-form-item label-width="168px" :disabled="true" label="离店日期">
              <el-input v-model="form.date2"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex" class="row-bg" justify="center">
          <span>需交付房费押金费共：{{ form.deposit }}</span>
        </el-row>
        <el-row type="flex" class="row-bg" justify="center">
          <el-col :span="16">
          </el-col>
          <el-col :span="8">
            <router-link :to="{name: 'extension/extend'}">
              <el-button type="primary" class="btn_large">返回</el-button>
            </router-link>
            <router-link :to="{name: 'payment'}">
              <el-button type="primary" class="btn_large">去支付</el-button>
            </router-link>
          </el-col>
        </el-row>
      </el-form>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: '某某人',
        room_type: '大床房',
        room_no: '203',
        date1: '2018-01-12',
        date2: '2018-01-13',
        days: 1,
        x: '全日',
        rates: 200,
        deposit: 100
      }
    }
  },
  methods: {
    onSubmit() {
      this.$message('submit!')
    },
    onCancel() {
      this.$message({
        message: 'cancel!',
        type: 'warning'
      })
    }
  }
}
</script>

<style scoped>
</style>



